import styled from 'styled-components';

export default styled.div`
  width: 30px;
  height: 30px;
  position: absolute;
  top: 14px;
  left: 32px;
  border-radius: 50%;
  border: 4px solid #fff;
  cursor: pointer;
  opacity: ${props => props.isHovered || props.isChecked ? '1' : '0'};
  transition: .3s;
  background-color: ${props => props.isChecked ? 'green' : 'transparent'}
`;


