import styled from 'styled-components';

export default styled.div`
  width: calc(33.333% - 30px);
  height: 324px;
  margin: 0 15px 34px;
  border-radius: 3px;
  box-shadow: 1px 1px 4px 2px rgba(0,0,0,.2);
  position: relative;
  background: url('${(props => props.imageUrl)}') no-repeat top;
  background-size: 100% calc(100% - 64px);
  
  &:hover {
    box-shadow: 1px 1px 5px 5px rgba(0,0,0,.2);
  }
  
  &::before {
    content: '';
    width: 100%;
    height: calc(100% - 64px);
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 64px;
    left: 0;
    background-color: rgba(0,0,0,.5);
    opacity: ${props => props.isHovered || props.isChecked ? '1' : '0'};
    transition: .3s;
  }
`;


