import React from 'react';

import Gallery from '../../components/Gallery/Gallery'
import Footer from '../../components/Footer/Footer';

import getPhotos from '../../http/getPhotos';

class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      photos: formatStatePhotos(getPhotos()),
      allSelected: false,
    }
  }

  render () {
    return (
      <div>
        <Gallery
          photos={this.state.photos}
          checkPhoto={this.checkPhoto.bind(this)}
          deletePhoto={this.deletePhoto.bind(this)}
          imageAmount={this.state.photos.length}
        />
        <Footer
          selectedImagesAmount={this.getSelectedCount()}
          deleteSelected={this.deleteSelected.bind(this)}
          selectAll={this.selectAll.bind(this)}
        />
      </div>
    );
  }

  checkPhoto(id, flag) {
    const index = this.state.photos.findIndex(photo => (photo.item_id === id));
    const photoStateCopy = [...this.state.photos];
    photoStateCopy[index].isChecked = flag;

    this.setState({
      photos: photoStateCopy,
    });
  }

  deletePhoto(id) {
    this.setState({
      photos: this.state.photos.filter(item => item.item_id !== id),
    }, () => {console.log(`photo id: ${id} was deleted`)});
  }

  deleteSelected() {
    this.setState({
      photos: this.state.photos.filter(item => !item.isChecked),
    }, () => {console.log(`all selected photos was deleted`)});
  }

  selectAll() {
    const photoStateCopy = [...this.state.photos];
    photoStateCopy.forEach(photo => photo.isChecked = !this.state.allSelected);

    this.setState({
      photos: photoStateCopy,
      allSelected: !this.state.allSelected,
    });
  }

  getSelectedCount() {
    let result = 0;

    this.state.photos.forEach(item => {
      if (item.isChecked) {
        result += 1;
      }
    });
    return result;
  }
}

function formatStatePhotos(photos) {
  const additionFields = {
    isChecked: false,
  };

  return (photos.map(photo => ({
    ...photo, ...additionFields
  })));
}

export default HomePage;


