import React from 'react';

import Container from './Container';
import Footer from './Footer';
import Checkbox from './Checkbox';
import UiButtons from './UiButtons';
import UiButton from './UiButton';

class Item extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isHovered: false,
    }
  }

  render () {
    const photo = this.props.photo;
    return (
      <Container
        onMouseEnter={() => this.setState({isHovered: true})}
        onMouseOver={() => this.setState({isHovered: true})}
        onMouseLeave={() => this.setState({isHovered: false})}
        isHovered={this.state.isHovered}
        isChecked={photo.isChecked}
        imageUrl={photo.sample_url}>
        <Checkbox
          isHovered={this.state.isHovered}
          isChecked={photo.isChecked}
          onClick={() => this.props.checkPhoto(photo.item_id, !photo.isChecked)}
        />
        <UiButtons isHovered={this.state.isHovered}>
          <UiButton className="fa fa-share-alt fa-2x fa-inverse"/>
          <UiButton
            className="fa fa-trash-o fa-2x fa-inverse"
            onClick={() => this.props.deletePhoto(photo.item_id)}
          />
          <UiButton className="fa fa-download fa-2x fa-inverse"/>
        </UiButtons>
        <Footer />
      </Container>
    );
  }
}

export default Item;


